body {
  margin: 0;
}
.flexContainer {
  margin: 50px;
  top: 0px;
  left: 0px;
  width: 1410px;
  height: 620px;
  background: #F2F2F2 0% 0% no-repeat padding-box;
  opacity: 1;
}

.inputStyle {
  padding: 10px;
  display: inline-block;
  width: 30%;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 6px;
  opacity: 1;
  top: 250px;
  width: 1100px;
  height: 40px;
  margin-top: 50px;
  margin-left: 120px;
}
.search-btn {
  border: none;
  background: Black;
  cursor: pointer;
  position: relative;
  color: white;
  top: 1.5px;
  left: 10px;
  width: 70px;
  height: 40px;
  border-radius: 6px;
}
